<template>
  <div class="list-container" ref="listContainer">
    <div class="event-list">
      <Collapse accordion v-model="openName">
        <Panel v-for="item in eventList" :key="item.id" :name="item.id+''" :ref="item.id+''">
          <span :style="{fontWeight:calNewEvent(item)? 800:'normal'}">
            {{ item.name }}
            <icon name="hot" class="text-icon" v-if="calNewEvent(item)"></icon>
          </span>
          <List slot="content">
            <ListItem v-for="(news,index)  in item.newsList" :key="item.id+'--'+index">
              <li style="cursor: pointer;" @click="turnToUrl(news.url)">
                {{ news.title }}
              </li>
            </ListItem>
          </List>
        </Panel>
      </Collapse>
    </div>
  </div>
</template>

<script>
import eventConfig from "../eventConfig";

let newEventSteps = eventConfig.event.newBreakOut

export default {
  data() {
    return {
      eventList: {},
      maxTime: 0,
      openName: '',
    }
  },
  components: {},
  methods: {
    openEvent(eventId) {
      let el = this.$refs[eventId][0].$el
      this.openName = eventId
      //等待手风琴面板折叠上之后，再滚动
      setTimeout(() => {
        el.scrollIntoView({behavior: 'smooth'});
      }, 300)
    },
    turnToUrl(url) {
      window.open(url)
    },
    calNewEvent(event) {
      return this.maxTime - newEventSteps <= event.firstReportTime
    },
    setData(newData, maxTime) {
      this.eventList = newData.sort(function (a, b) {
        return b.firstReportTime - a.firstReportTime
      })
      this.maxTime = maxTime
      this.$refs.listContainer.scroll(0, 0);
    }
  },
}
</script>
<style>
.list-container {
  width: 100%;
  height: 100%;
  overflow: auto;
}

.event-list {
  text-align: left;
  width: 100%;
  overflow: hidden;
}

.event-list >>> .ivu-collapse > .ivu-collapse-item > .ivu-collapse-header {
  background-color: rgba(19, 25, 47, 0.6)
}

.event-list .ivu-collapse-item:last-child {
  background-color: rgba(19, 29, 47, 0.5);
}

.event-list .ivu-collapse-content {
  background-color: rgba(19, 29, 47, 0.5);
}

.event-list .ivu-collapse > .ivu-collapse-item > .ivu-collapse-header {
  color: inherit;
  background-color: #071225;
}

.event-list .ivu-collapse {
  background: transparent;
}

/* 滚动条默认样式 */
.list-container::-webkit-scrollbar { /*滚动条整体样式*/
  width: 5px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 1px;
}

.list-container::-webkit-scrollbar-thumb { /*滚动条里面小方块*/
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(46, 121, 211, 0.7);
}

.list-container::-webkit-scrollbar-track { /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  background: #060a0e;
}

.list-container::-webkit-scrollbar-thumb:hover {
  background: #888888; /*鼠标移上滚动条样式*/
}
</style>
