<template>
  <div style="position:relative;height: 100%;width: 100%">
    <Echart
        :options="options"
        id="timelineBar"
        height="100%"
        width="100%"
        ref="timelineBar"
    ></Echart>
    <div style="position: absolute;bottom: 15px;left:calc( 80% + 10px ) ;">
      <span>
        {{ formatDate(currentTime) }}
      </span>
      /
      <span>
        {{ formatDate(maxTime) }}
      </span>
    </div>
    <Slider v-model="currentIndex" style="position: absolute;bottom: 0;left: 10%;right: 20%"
            :min="-1"
            :max="barData.length-1"
            :step="1"
            :tip-format="formatDateByIndex"
            @on-change="sliderChangedByUser"
            ref="slider"
    ></Slider>
  </div>
</template>

<script>
import Echart from '@/common/echart'
import dateFormat from 'dateformat';
import {debounce} from "debounce";
import eventConfig from "../eventConfig";

let format = eventConfig.dateFormat
let step = eventConfig.event.oneStep; //slider间隔
export default {
  name: "timelineBar",
  components: {
    Echart,
  },
  props: ['autoStart'],
  data() {
    return {
      currentIndex: -1,
      barData: [],
      step,
      options: {
        animation: false,
        toolbox: {
          show: true,
          feature: {
            // magicType: {type: ['line', 'bar']},
            myStart: {
              show: this.showButton('myStart'),
              title: '开始演化',
              icon: 'path://M128 138.666667c0-47.232 33.322667-66.666667 74.176-43.562667l663.146667 374.954667c40.96 23.168 40.853333 60.8 0 83.882666L202.176 928.896C161.216 952.064 128 932.565333 128 885.333333v-746.666666z',
              onclick: () => {
                this.autoStatusChange(true)
              }
            },
            myStop: {
              show: this.showButton('myStop'),
              title: '停止演化',
              icon: 'path://M760 960H264c-110.5 0-200-89.5-200-200V264c0-110.5 89.5-200 200-200h496c110.5 0 200 89.5 200 200v496c0 110.5-89.5 200-200 200z',
              onclick: () => {
                this.autoStatusChange(false)
              }
            },
            myBackward: {
              show: this.showButton('myBackward'),
              title: '后退一步',
              icon: 'path://M 60.8832 560.974 c -8.94344 -5.76401 -14.8324 -15.8114 -14.8324 -27.2536 c 0.035431 -11.4031 5.98312 -21.4551 14.9573 -27.1902 l 432.93 -276.67 c 9.62315 -6.13416 22.2309 -6.99849 32.957 -1.10861 c 10.7346 5.88708 16.8072 16.9564 16.8072 28.3939 v 164.663 l 372.566 -198.422 c 9.6791 -5.14863 21.7665 -5.23814 31.8783 0.835417 c 10.0782 6.04279 15.6921 16.7373 15.6921 27.7142 V 818.623 c 0 11.0087 -5.64373 21.7367 -15.7555 27.7758 c -10.1434 6.04186 -22.2616 5.91878 -31.9407 0.708613 L 543.702 646.712 v 166.637 c 0 11.4683 -6.10806 22.5712 -16.8669 28.4247 a 32.2988 32.2988 0 0 1 -33.0186 -1.22982 L 60.8832 560.974 Z m 0 0',
              onclick: () => {
                this.autoStatusChange(false)
                this.backward()
              }
            },
            myForward: {
              show: this.showButton('myForward'),
              title: '前进一步',
              icon: 'path://M948.045553 509.121716c8.943443 5.764008 14.832391 15.811396 14.832391 27.254566-0.035431 11.402144-5.984051 21.455127-14.957331 27.189298L514.99157 840.235176c-9.623152 6.134165-22.231804 6.998487-32.957969 1.108607-10.733624-5.887083-16.807184-16.957298-16.807184-28.393008V648.288668L92.660023 846.710595c-9.679096 5.147701-21.765611 5.23721-31.878266-0.83635-10.078157-6.041859-15.692983-16.736323-15.692983-27.714232V251.472787c0-11.008678 5.64373-21.736707 15.756385-27.775769 10.142491-6.042792 22.260708-5.918784 31.939803-0.709545l372.440523 200.395787V256.746361c0-11.467411 6.108059-22.571192 16.867789-28.423777a32.295042 32.295042 0 0 1 33.018574 1.229816l432.933705 279.569316z m0 0',
              onclick: () => {
                this.autoStatusChange(false)
                this.forward()
              }
            },
          },
          top: 15,
          left: '80%',
          iconStyle: {
            normal: {
              color: "#c8c8c8",
              borderColor: "#c0c0c0",
              borderCap: 'round',
            },
            emphasis: {
              borderColor: 'rgb(255,255,255)',
              borderWidth: 1,
              textPosition: 'top'
            }
          },
        },
        tooltip: {
          trigger: 'axis',
          type: 'shadow',
          formatter(params) {
            let data = params[0].data
            return dateFormat(data[0], format) + ': ' + data[1]
          }
        },
        grid: [
          {left: '10%', top: 0, bottom: '20px', right: '20%'}
        ],
        xAxis: {
          type: 'time',
          boundaryGap: false,
          show: false,
        },
        yAxis: {
          type: 'value',
          boundaryGap: [0, '100%'],
          show: false
        },
        series: [
          {
            name: '污染事件数量',
            type: 'bar',
            data: [],
            itemStyle: {
              emphasis: {
                barBorderRadius: 7
              },
              normal: {
                barBorderRadius: 7,
                color: (params) => {
                  if (params.data[0] <= this.currentTime) {
                    return new this.$echarts.graphic.LinearGradient(
                        0, 0, 1, 0,
                        [
                          {offset: 0, color: 'rgba(57,119,230,0.8)'},
                          {offset: 1, color: 'rgba(59,187,248,0.8)'}

                        ]
                    )
                  }
                  return 'grey'
                }
              }
            }
          }
        ]
      }
    }
  },
  watch: {
    currentTime() {
      let myChart = this.$refs.timelineBar.chart
      myChart.setOption({})
      // this.debounceEmitPointerChanged()
      this.emitPointerChanged()
    },
  },
  computed: {
    maxTime: {
      get() {
        let dataLength = this.barData.length
        return dataLength > 0 ? this.barData[dataLength - 1][0] : -1
      },
      set(v) {
        this.maxTime = v
      }
    },
    currentTime: {
      get() {
        if (this.currentIndex < 0 && this.barData[0])
          return this.barData[0][0].getTime() - step
        if (this.currentIndex >= this.barData.length && this.barData.length > 0)
          return this.barData[this.barData.length - 1][0].getTime() + step
        return this.barData[this.currentIndex] ? this.barData[this.currentIndex][0] : -1
      },
      set(v) {
        this.currentTime = v
      }
    }
  },
  mounted() {
  },
  methods: {
    showButton() {
      // if (type === 'myStart') {
      //   return !this.autoStart
      // }
      // if (type === 'myStop') {
      //   return this.autoStart
      // }
      // if (type === 'myBackward') {
      //   return this.currentIndex > -2
      // }
      // if (type === 'myForward') {
      //   return this.barData && this.currentIndex < this.barData.length
      // }
      return true
    },
    sliderChangedByUser() {
      this.autoStatusChange(false)
    },
    autoStatusChange(status) {
      this.$emit("autoStatusChanged", status)
    },
    debounceEmitPointerChanged: debounce(function () {
      this.$emit('pointerChanged', this.currentTime)
    }),
    emitPointerChanged() {
      this.$emit('pointerChanged', this.currentTime)
    },
    formatDate(value) {
      return dateFormat(new Date(value), format)
    },
    formatDateByIndex(index) {
      if (index < 0 && this.barData[0])
        return dateFormat(this.barData[0][0].getTime() - step, format)
      return dateFormat(new Date(this.barData[index] ? this.barData[index][0] : -1), format)
    },
    forward() {
      if (this.currentIndex >= this.barData.length - 1) {
        this.autoStatusChange(false)
        return
      }
      this.currentIndex++
      if (this.currentIndex >= this.barData.length)
        this.$emit('maxPointer', this.currentTime)// 这个currenttime，已经是currentIndex=barData.length时的time了
    },
    backward() {
      if (this.currentIndex <= -1)
        return
      // if (this.currentIndex > -1)
      this.currentIndex--
      if (this.currentIndex < -1)
        this.$emit('minPointer', this.barData[0][0].getTime())
    },
    setData(data, pointerPosition) {
      let barData = []
      let max = 0
      for (let time in data) {
        let value = Object.keys(data[time]).length
        max = Math.max(value, max)
        barData.push([new Date(time * 1), value])
      }
      this.barData = barData.sort((a, b) => {
        return a[0].getTime() - b[0].getTime()
      })

      if (pointerPosition === 'min')
        this.currentIndex = -1
      if (pointerPosition === 'max')
        this.currentIndex = this.barData.length - 1
      let myChart = this.$refs.timelineBar.chart
      let option = myChart.getOption()
      option.series[0].data = this.barData
      option.yAxis.max = max
      // option.toolbox[0].feature.myStart.show = this.showButton('myStart')
      // option.toolbox[0].feature.myStop.show = this.showButton('myStop')
      // option.toolbox[0].feature.myBackward.show = this.showButton('myBackward')
      // option.toolbox[0].feature.myForward.show = this.showButton('myForward')
      myChart.setOption(option)
      this.$refs.slider.$forceUpdate()
    }
  }
}
</script>

