<template>
  <div style="width: 100%;height: 100%;overflow-y:auto;position: relative" class="list-container">
<!--    <span style="position: absolute;top: 0px;left: 20px;z-index: 10000">-->
<!--      单系列模式：-->
<!--       <i-switch v-model="singleMode">-->
<!--      <span slot="open">开</span>-->
<!--      <span slot="close">关</span>-->
<!--    </i-switch>-->
<!--    </span>-->

    <Echart
        :options="options"
        ref="echart"
        style="height: 100%"
    ></Echart>
  </div>
</template>

<script>
import Echart from '@/common/echart'
import dateFormat from 'dateformat';

export default {
  name: "pollutionVsTime",
  components: {
    Echart
  },
  watch: {
    singleMode(newVal) {
      console.log(newVal)
      this.options.legend.selectedMode = newVal ? 'single' : true
    }
  },
  data() {
    return {
      singleMode: false,
      options: {
        legend: {
          show:false
          // selectedMode: true,
          // type: 'scroll',
          // orient: 'vertical',
          // right: 0,
          // top: 0,
          // bottom: 20,
          // width: 150,
          // pageIconColor: "#eee",
          // pageTextStyle: {
          //   color: '#eee'
          // }
        },
        tooltip: {
          trigger: 'axis',
          formatter: (params) => {
            let tooltipStr = '<p>'
            tooltipStr += dateFormat(params[0].data[0], 'yyyy-mm') + '<br/>' // 时间
            if (!this.singleMode)
              tooltipStr += '<small style="font-size: 50%;color: #aaa">(仅展示非0值)</small><br/>'
            for (let i in params) {
              let param = params[i]
              if (param.data[1] <= 0 && !this.singleMode)
                continue;
              tooltipStr += param.marker + param.seriesName + ': ' + param.data[1] + '<br/>'
            }
            tooltipStr += '</p>'
            return tooltipStr
          }
        },
        grid: [
          {left: 50, top: 20, bottom: 30, right: 20},
        ],
        xAxis: {
          type: 'time',
        },
        yAxis: {
          name: '事件数量',
          nameLocation: 'center'
        },
        series: []
      }
    }
  },
  methods: {
    setData(newData, pollutionType2Color) {
      let time2Pollution2Value = {}
      let timePoints = []
      let pollutions = []
      // 这个time必须是string类型
      for (let i in newData) {
        let realTime = new Date(newData[i].timeRange[0] * 1)
        //这里按月统计，所以每个时间点都变成当月1号，再去组织map
        let monthStart = new Date(realTime.getFullYear(), realTime.getMonth(), 1).getTime()
        let mapKeyTime = monthStart

        if (!time2Pollution2Value[mapKeyTime])
          time2Pollution2Value[mapKeyTime] = {}
        if (timePoints.indexOf(mapKeyTime) < 0)
          timePoints.push(mapKeyTime)

        let {pollution} = newData[i]
        if (pollutions.indexOf(pollution) < 0)
          pollutions.push(pollution)
        if (!time2Pollution2Value[mapKeyTime][pollution])
          time2Pollution2Value[mapKeyTime][pollution] = 0
        time2Pollution2Value[mapKeyTime][pollution]++
      }
      // 给所有时间点排个序，保证从老时间点到新时间点排列
      timePoints.sort((a, b) => {
        return a * 1 - b * 1
      })
      //pollution排个序，从而保证color的顺序、pollution legend的顺序，series的顺序，以及map上散点图的顺序，都是对应的
      pollutions.sort()
      this.options.color = (() => {
        let colors = []
        for (let i in pollutions)
          colors.push(pollutionType2Color[pollutions[i]])
        return colors

      })()
      let seriesList = []
      for (let i in pollutions) {
        let pollution = pollutions[i]
        let lineData = []
        for (let j in timePoints) {
          let time = timePoints[j]
          lineData.push([new Date(timePoints[j] * 1), time2Pollution2Value[time][pollution] || 0])
        }
        seriesList.push({
          type: 'line',
          showSymbol: false,
          name: pollution,
          labelLayout: {
            moveOverlap: 'shiftY'
          },
          emphasis: {
            focus: 'series'
          },
          data: lineData
        });
      }
      this.options.series = seriesList
    }
  }
}
</script>

