<template>
  <div id="index" ref="appRef">
    <init-modal :show="showInitModal" @formSubmit="initFormSubmitted"></init-modal>
    <div class="bg">
      <transition name="fade">
        <dv-loading v-if="loading">Loading...</dv-loading>
      </transition>
      <div class="host-body outer">
        <div class="d-flex jc-center head">
          <dv-decoration-10 class="dv-dec-10"/>
          <div class="d-flex jc-center">
            <dv-decoration-8 class="dv-dec-8" :color="['#568aea', '#000000']"/>
            <div class="title">
              <span class="title-text">网络舆情环境问题识别与风险分析</span>
              <dv-decoration-6
                  class="dv-dec-6"
                  :reverse="true"
                  :color="['#50e3c2', '#67a1e5']"
              />
            </div>
            <dv-decoration-8
                class="dv-dec-8"
                :reverse="true"
                :color="['#568aea', '#000000']"
            />
          </div>
          <dv-decoration-10 class="dv-dec-10-s"/>
        </div>
        <div class="d-flex jc-between px-2">
          <div class="d-flex aside-width">
            <div class="react-left ml-4 react-l-s">
              <span class="react-before"></span>
              <button-group class="text">
                <Button
                    :type="showEventList? 'primary':'default'"
                    icon="ios-list"
                    :class="showEventList? 'card-view-active':'card-view-normal'"
                    @click="showEventList=!showEventList"
                    ghost
                ></Button>
                <Button
                    :type="showCardView4? 'primary':'default'"
                    icon="ios-funnel-outline"
                    :class="showCardView4? 'card-view-active':'card-view-normal'"
                    @click="showCardView4=!showCardView4"
                    ghost
                ></Button>
                <!--                <Button-->
                <!--                    :type="showCardView5? 'primary':'default'"-->
                <!--                    icon="ios-pie-outline"-->
                <!--                    :class="showCardView5? 'card-view-active':'card-view-normal'"-->
                <!--                    @click="showCardView5=!showCardView5"-->
                <!--                    ghost-->
                <!--                ></Button>-->
                <!--                <Button-->
                <!--                    :type="showCardView6? 'primary':'default'"-->
                <!--                    icon="md-trending-up"-->
                <!--                    :class="showCardView6? 'card-view-active':'card-view-normal'"-->
                <!--                    @click="showCardView6=!showCardView6"-->
                <!--                    ghost-->
                <!--                ></Button>-->
                <Button
                    :type="showCardView7? 'primary':'default'"
                    icon="md-trending-up"
                    :class="showCardView7? 'card-view-active':'card-view-normal'"
                    @click="showCardView7=!showCardView7"
                    ghost
                ></Button>
                <Button
                    type="primary"
                    icon="ios-download-outline"
                    class="card-view-active"
                    @click="downloadReport"
                    ghost
                >下载报告
                  <!-- 无需显示该router-link元素，只需要在点击了下载报告按钮后触发该链接 到新页面进行下载 -->
                  <router-link
                      style="width: 0;height: 0"
                      id="downLoadLink"
                      v-show="false"
                      target="_blank"
                      :to="{path: '/downloadReport', query: {from: timeFrom, to: timeTo, url: downloadReportUrl, token: downloadReportToken}}"
                  ></router-link>
                </Button>
              </button-group>
            </div>
            <!--            <div class="react-right ml-3">-->
            <!--              <span class="text">数据分析2</span>-->
            <!--            </div>-->
          </div>
          <div class="d-flex aside-width">
            <div class="react-right mr-1">

            </div>
            <div class="react-right mr-6 react-l-s">
              <span class="react-after"></span>
              <span class="text">
              <DatePicker v-model="timeRange" type="daterange" placeholder="选择时间范围" transfer
                          @on-ok="timeRangeChanged" confirm></DatePicker>
              </span>
            </div>
          </div>
        </div>
        <div class="body-box content">
          <div class="main-content">
            <div class="left-bar">
              <transition-group
                  name="flip-list"
                  tag="div"
                  v-bind:css="false"
                  v-on:before-enter="beforeEnter"
                  v-on:enter="enter"
                  v-on:leave="leave"
              >
                <card-view class="card-view-active" title="新闻列表" icon="align-left" key="eventList"
                           @minimize="onMinimize('eventList')"
                           v-show="showEventList">
                  <div slot="content" style="width: 100%;height: calc(99.9vh - 282px)">
                    <eventList ref="eventListComponent"></eventList>
                  </div>
                </card-view>
              </transition-group>

            </div>
            <div class="right-bar">
              <transition-group
                  name="flip-list"
                  class="card-view"
                  tag="div"
                  v-bind:css="false"
                  v-on:before-enter="beforeEnter"
                  v-on:enter="enter"
                  v-on:leave="leave"
                  enter-active-class="card-view"
                  leave-active-class="card-view"
              >
                <card-view class="card-view-active" title="事件筛选" icon="filter" key="cardView4"
                           @minimize="onMinimize('cardView4')"
                           v-show="showCardView4">
                  <div slot="content" style="width: 100%;height: calc(33.3vh - 140px)">
                    <event-filter ref="eventFilter" @dataChanged="eventFilterChanged"></event-filter>
                  </div>
                </card-view>
                <card-view class="card-view-active" title="污染分析" icon="chart-line" key="cardView7"
                           @minimize="onMinimize('cardView7')"
                           v-show="showCardView7">
                  <div slot="content" style="width: 100%;height: calc(66.6vh - 215px)">
                    <Tabs value="name3">
                      <TabPane label="污染vs行业分析" name="name3">
                        <div style="width: 100%;height: calc(66.6vh - 300px)">
                          <proportion ref="proportionChart"/>
                        </div>
                      </TabPane>
                      <TabPane label="污染vs时间分析" name="name2">
                        <pollution-vs-time ref="pollutionVsTime"/>
                      </TabPane>
                      <TabPane label="行业vs时间分析" name="name1">
                        <div style="width: 100%;height: calc(66.6vh - 300px)">
                          <industry-vs-time ref="industryVsTime"></industry-vs-time>
                        </div>
                      </TabPane>
                    </Tabs>
                  </div>
                </card-view>
              </transition-group>

            </div>
            <div id="map-box">
              <amap-echarts ref="map" @eventClick="mapEventClick"></amap-echarts>
            </div>
            <div style="position: fixed;bottom: 5px;left:0;right:0;width: 100%;height: 80px">
              <timeline-bar ref="timeline" @pointerChanged="timePointerChanged"
                            @autoStatusChanged="autoStatusChanged" @maxPointer="appendData"
                            @minPointer="prependData" :auto-start="auto"></timeline-bar>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import Velocity from 'velocity-animate'
import cardView from "../components/cardView";
import eventList from '../components/eventList'
import amapEcharts from "../components/map/amapEcharts";
import timelineBar from "../components/timelineBar";
import eventFilter from "../components/eventFilter";
import proportion from "../components/proportion";
import initModal from "../components/initModal"
import eventConfig from "../eventConfig";
import dataApi from "../api/dataApi";
import industryVsTime from "../components/industryVsTime";
import pollutionVsTime from "../components/pollutionVsTime";
import PollutionVsTime from "../components/pollutionVsTime";

let requestInterval = eventConfig.requestInterval //自动演化时间间隔
let timeoutFunction = null
let pollutionType2Color = {
  '大气污染':'#91cc75',
  '水污染':'#fac858',
  '土壤污染':'#5470c6',
  '固废污染':'#ff4500',
  '生态破坏':'#009db2',
  '其他':'#ffb0b0'
}

function randomColor() {
  let r = Math.floor(Math.random() * 256)
  let g = Math.floor(Math.random() * 256)
  let b = Math.floor(Math.random() * 256)
  return 'rgb(' + r + ',' + g + ',' + b + ')'
}

export default {
  // mixins: [drawMixin],
  data() {
    return {
      showInitModal: true,
      timeRange: [],
      showEventList: true,
      showCardView4: true,
      showCardView5: true,
      showCardView6: true,
      showCardView7: true,
      auto: true,
      timing: null,
      loading: true,
      time2EventId2Event: {},
      time2EventId2EventKeys: [],//time2EventId2Event的key，即所有事件的时间，正序排列
      currentTime: 0,
      currentFilter: {industryValues: [], pollutionValues: []},
      maxTime: 0,
      timeFrom: '',
      timeTo: '',
      downloadReportUrl: '',
      downloadReportToken: ''
    }
  },
  components: {
    PollutionVsTime,
    cardView,
    eventList,
    amapEcharts,
    timelineBar,
    proportion,
    eventFilter,
    initModal,
    industryVsTime,
    pollutionVsTime
  },
  mounted() {
    this.loading = true
  },
  watch: {
    auto: {
      immediate: true,
      handler(newVal) {
        if (newVal)
          this.startAuto()
        else
          this.stopAuto()
      }
    }
  },
  methods: {
    async downloadReport(){
      document.getElementById('downLoadLink').click()
    },
    initFormSubmitted(formValue) {
      dataApi.setRequestUrl(formValue.url)
      dataApi.setLimit(formValue.limit)
      dataApi.setToken(formValue.token)
      this.downloadReportUrl = formValue.downloadReportUrl
      this.downloadReportToken = formValue.downloadReportToken
      this.timeRange = [...formValue.timeRange]
      this.showInitModal = false
      this.timeRangeChanged()
    },
    mapEventClick(eventId) {
      this.auto = false
      this.$refs.eventListComponent.openEvent(eventId)
    },
    timeRangeChanged() {
      // let value = this.timeRange
      // if ((value[1].getTime() - value[0].getTime()) < (eventConfig.maxRequestStep * eventConfig.event.oneStep)) {
      this.init(this.timeRange)
      // } else {
      //   this.$Notice.error({
      //     title: '时间设置错误',
      //     desc: '时间范围不得超过' + eventConfig.maxRequestStep + eventConfig.stepUnit
      //   });
      // }
    },
    async init(timeRange) {
      this.loading = true
      let from = timeRange ? timeRange[0].getTime() : new Date().getTime() - eventConfig.initTimeRange
      let to = timeRange ? timeRange[1].getTime() : undefined
      this.timeFrom = from
      this.timeTo = to
      let initData = await this.requestData(from, to)
      this.time2EventId2Event = {...initData}
      this.time2EventId2EventKeys = Object.keys(this.time2EventId2Event).sort((a, b) => {
        return a * 1 - b * 1
      })
      this.initPollutionType2Color(initData)
      this.initTimeline(initData, this.time2EventId2EventKeys)
      this.initFilter(initData)
      // 这个图表不需要随演化更新，所以只用在初始化时开始更新
      this.dataUpdate()
      this.loading = false
      this.auto = true
    },
    initPollutionType2Color(time2EventId2Event) {
      // 初始化每种污染对应的颜色
      for (let time in time2EventId2Event) {
        for (let eventId in time2EventId2Event[time]) {
          let {pollution} = time2EventId2Event[time][eventId]
          if (!pollutionType2Color[pollution])
            pollutionType2Color[pollution] = randomColor()
        }
      }
    },
    async requestData(from, to) {
      let oneStep = eventConfig.event.oneStep
      from = from - from % oneStep
      to = to - to % oneStep

      let data = await dataApi.requestData(from, to)

      let result = data.output
      //let result = await dataApi.mockRequest(from, to)


      let newResult = {}
      // 这里，如果一个step内，出现了两个result，需要把这俩合并
      for (let time in result) {
        // 要减掉的剩下的时间，强制给他取整
        let newTime = time - time % oneStep
        if (!newResult[newTime])
          newResult[newTime] = {}
        newResult[newTime] = {
          ...newResult[newTime],
          ...result[time]
        }
      }
      result = {...newResult}


      // 这里的firstReportTime即这个time里所有的新闻的最早发生时间
      for (let time in result) {
        for (let eventId in result[time]) {
          result[time][eventId].firstReportTime = this.getFirstReportTime(result[time][eventId])
        }
      }

      // 给不存在的时间点填上空数据，保证slider间隔正常

      for (let i = from; i < to; i += oneStep) {
        if (!result[i])
          result[i] = {}
      }
      return result
    },
    prependData(toTime) {
      let sliderPointerBefore = this.currentTime
      let fromTime = toTime - eventConfig.event.oneStep
      this.requestData(fromTime, toTime).then((data) => {
        if (this.currentTime !== sliderPointerBefore) return //slider在请求期间有改变，则说明用户做了别的操作，请求到的最新数据不往里加
        this.time2EventId2Event = {
          ...this.time2EventId2Event,
          ...data
        }
        this.time2EventId2EventKeys = Object.keys(this.time2EventId2Event).sort((a, b) => {
          return a * 1 - b * 1
        })
        if (this.time2EventId2EventKeys.length > eventConfig.bufferSize) {
          let removeTime = this.time2EventId2EventKeys.pop()
          delete this.time2EventId2Event[removeTime]
        }
        this.$refs.eventFilter.setData(this.time2EventId2Event, true)
        this.$refs.timeline.setData(this.time2EventId2Event, 'min')
      })
    },
    appendData(fromTime) {// 给timeline加一格的数据(fromTime往后一格)
      let sliderPointerBefore = this.currentTime
      let toTime = fromTime + eventConfig.event.oneStep
      //这里时间往后到当前时间之后，也是允许的，没做限制
      this.requestData(fromTime, toTime).then((data) => {
        if (this.currentTime !== sliderPointerBefore) return //slider在请求期间有改变，则说明用户做了别的操作，请求到的最新数据不往里加
        this.time2EventId2Event = {
          ...this.time2EventId2Event,
          ...data
        }
        this.time2EventId2EventKeys = Object.keys(this.time2EventId2Event).sort((a, b) => {
          return a * 1 - b * 1
        })
        if (this.time2EventId2EventKeys.length > eventConfig.bufferSize) {
          let removeTime = this.time2EventId2EventKeys.shift()
          delete this.time2EventId2Event[removeTime]
        }
        this.$refs.eventFilter.setData(this.time2EventId2Event, true)
        this.$refs.timeline.setData(this.time2EventId2Event, 'max')
      })
    },
    startAuto() {
      if (!this.auto) return
      if (timeoutFunction)
        clearTimeout(timeoutFunction)
      if (this.$refs.timeline)
        this.$refs.timeline.forward()
      timeoutFunction = setTimeout(() => {
        this.startAuto()
      }, requestInterval)
    },
    stopAuto() {
      if (this.auto) return
      if (timeoutFunction)
        clearTimeout(timeoutFunction)
    },
    autoStatusChanged(autoStatus) {
      this.auto = autoStatus
    },
    timePointerChanged(currentTime) {
      this.currentTime = currentTime
      this.dataUpdate()
    },
    eventFilterChanged(currentFilter) {
      this.currentFilter = {...currentFilter}
      this.dataUpdate()
    },
    initFilter(data) {
      this.$refs.eventFilter.setData({...data}, false, pollutionType2Color)
    },
    initTimeline(data) {
      this.$refs.timeline.setData({...data}, 'max')
      // this.$refs.timeline.setData({...data}, 'min')
    },
    getFirstReportTime(event) {
      let minTime = Number.MAX_VALUE
      for (let i in event.newsList) {
        minTime = Math.min(event.newsList[i].reportTime, minTime)
      }
      return minTime
    },
    dataUpdate() {
      let filteredData = {} // id2event
      for (let time in this.time2EventId2Event) {
        if (time * 1 > this.currentTime) continue
        filteredData = {...filteredData, ...this.time2EventId2Event[time]}
      }
      let results = Object.values(filteredData).filter(event => {
        let industry = event.industry
        let pollution = event.pollution
        return this.currentFilter.industryValues.indexOf(industry) >= 0 && this.currentFilter.pollutionValues.indexOf(pollution) >= 0
      })

      this.$refs.map.setData(results, this.currentTime, pollutionType2Color)
      this.$refs.eventListComponent.setData(results, this.currentTime, pollutionType2Color)
      this.$refs.proportionChart.setData(results, this.currentTime, pollutionType2Color)
      this.$refs.industryVsTime.setData(results)
      this.$refs.pollutionVsTime.setData(results, pollutionType2Color)

    },
    onMinimize(tableName) {
      if (tableName === 'eventList')
        this.showEventList = false

      if (tableName === 'cardView2')
        this.showCardView2 = false

      if (tableName === 'cardView3')
        this.showCardView3 = false

      if (tableName === 'cardView4')
        this.showCardView4 = false

      if (tableName === 'cardView5')
        this.showCardView5 = false

      if (tableName === 'cardView6')
        this.showCardView6 = false
      if (tableName === 'cardView7')
        this.showCardView7 = false

    },
    beforeEnter: function (el) {
      el.style.opacity = 0
      el.style.height = 0
    },
    enter: function (el, done) {
      setTimeout(function () {
        Velocity(
            el,
            {opacity: 1, height: '100%'},
            {complete: done}
        )
      }, 50)
    },
    leave: function (el, done) {
      setTimeout(function () {
        Velocity(
            el,
            {opacity: 0, height: 0},
            {complete: done}
        )
      }, 50)
    }
  }
}
</script>

<style lang="scss">
@import '../assets/scss/index.scss';

.fade-enter-active, .fade-leave-active {
  transition: opacity .8s;
}

.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */
{
  opacity: 0;
}

.card-view-active {
  pointer-events: auto;
}
</style>

