<template>
  <Modal
      v-model="show"
      title="配置服务"
      :mask-closable="false"
      :closable="false"
      id="configModal"
  >
    <Form :model="formValue" label-position="top" :rules="ruleValidate" ref="validateForm" style="padding:10px">
      <FormItem label="服务地址" prop="url">
        <Input v-model="formValue.url" placeholder="{host}:{port}/api/">
          <span slot="prepend">http://</span>
        </Input>
      </FormItem>
      <FormItem prop="timeRange" id="timeRangeFormItem">
        <span slot="label" style="line-height: 1.5;vertical-align: top">
          时间范围
        </span>
        <DatePicker v-model="formValue.timeRange" type="daterange" placeholder="选择时间范围" transfer
                    style="width: 100%"></DatePicker>
      </FormItem>
      <FormItem label="密钥" prop="token">
        <Input v-model="formValue.token" placeholder="(token)"></Input>
      </FormItem>
      <!--      <FormItem prop="limit" id="limitFormItem">-->
      <!--        <span slot="label" style="line-height: 1.5;vertical-align: top">-->
      <!--          结果集规模-->
      <!--          <small style="font-size: 50%;color: gray;margin-bottom: 2px">({{ minLimit }}~{{ maxLimit }})</small>-->
      <!--        </span>-->
      <!--        <InputNumber v-model="formValue.limit" style="width: 100%"></InputNumber>-->
      <!--      </FormItem>-->
      <FormItem label="报告下载服务地址" prop="url">
        <Input v-model="formValue.downloadReportUrl" placeholder="{host}:{port}/api/">
          <span slot="prepend">http://</span>
        </Input>
      </FormItem>
      <FormItem label="报告下载服务密钥" prop="token">
        <Input v-model="formValue.downloadReportToken" placeholder="(token)"></Input>
      </FormItem>
    </Form>
    <div slot="footer">
      <Button size="large" long @click="ok" ghost>确定</Button>
    </div>
  </Modal>
</template>
<script>
import eventConfig from "../eventConfig";

export default {
  props: ['show'],
  data() {
    console.log(eventConfig)
    return {
      maxRequestStep: eventConfig.maxRequestStep,
      stepUnit: eventConfig.stepUnit,
      minLimit: eventConfig.minLimit,
      maxLimit: eventConfig.maxLimit,
      formValue: {
        token: eventConfig.initModal.defaultToken,
        limit: eventConfig.defaultLimit,
        url: eventConfig.initModal.defaultUrl,
        timeRange: [...eventConfig.initModal.defaultTimeRange],
        downloadReportUrl: eventConfig.initDownloadReport.defaultDownloadReportUrl,
        downloadReportToken: eventConfig.initDownloadReport.defaultDownloadReportToken
      },
      ruleValidate: {
        url: [
          {message: '请填写服务地址', trigger: 'blur', required: true}
        ],
        timeRange: [
          {trigger: 'blur', validator: this.validateTimeRange, required: true},
          {trigger: 'blur', validator: this.validateMaxStep},
        ],
        token: [
          {trigger: 'blur', validator: this.validateToken, required: true}
        ],
        limit: [
          {trigger: 'blur', validator: this.validateLimit, required: true}
        ],
        downloadReportUrl: [
          {message: '请填写服务地址', trigger: 'blur', required: true}
        ],
        downloadReportToken: [
          {trigger: 'blur', validator: this.validateToken, required: true}
        ]
      }
    }
  },
  methods: {
    validateLimit(rule, value, callback) {
      if (value >= eventConfig.minLimit && value <= eventConfig.maxLimit)
        callback()
      else
        callback(new Error('请正确填写结果集规模（' + eventConfig.minLimit + '~' + eventConfig.maxLimit + '）'));

    },
    validateToken(rule, value, callback) {
      if (value.length > 0)
        callback()
      else
        callback(new Error('请填写密钥'));
    },
    validateTimeRange(rule, value, callback) {
      if (value.length >= 2 && value[0] instanceof Date && value[1] instanceof Date) {
        callback()
      } else {
        callback(new Error('请填写时间范围'));
      }
    },
    validateMaxStep(rule, value, callback) {
      // if ((value[1].getTime() - value[0].getTime()) < (eventConfig.maxRequestStep * eventConfig.event.oneStep)) {
      callback()
      // } else {
      //   callback(new Error('时间范围不得超过' + eventConfig.maxRequestStep + eventConfig.stepUnit));
      // }
    },
    ok() {
      this.handleSubmit()
    },
    handleSubmit() {
      this.$refs.validateForm.validate((valid) => {
        if (valid) {
          this.$emit('formSubmit', {...this.formValue, url: 'http://' + this.formValue.url})
        } else {
          this.$Message.error('配置信息有误!');
        }
      })
    }
  }
}
</script>
<style>
#configModal .ivu-modal-header {
  background-color: #2d2d2d
}

#configModal .ivu-modal-content {
  background-color: rgba(45, 45, 45, 0.6)

}

#configModal .ivu-btn-ghost.ivu-btn-dashed, .ivu-btn-ghost.ivu-btn-default {
  color: #e7e7e7;
  border-color: #e7e7e7
}

