<template>
  <div style="width: 100%;height: 100%;overflow-y:auto" class="list-container">
    <Echart
        :options="options"
        id="centreLeftChart"
        :height="height+'px'"
        ref="echart"
    ></Echart>
  </div>
</template>

<script>
import Echart from '@/common/echart'


export default {
  name: "proportion",
  components: {
    Echart
  },
  data() {
    return {
      height: 200,
      options: {
        animation: false,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // Use axis to trigger tooltip
            type: 'shadow' // 'shadow' as default; can also be 'line' or 'shadow'
          }
        },
        // legend: {},
        grid: {
          left: 50, top: 20, bottom: 30, right: 20
        },
        xAxis: {
          type: 'value',
        },
        yAxis: {
          type: 'category',
          data: []
        },
        series: []
      }
    }
  },
  methods: {
    setData(newData, currentTime, pollutionType2Color) {
      // //先清空所有数据
      this.options.series = []
      this.options.yAxis.data = []
      let pollutions = []
      let industries = []

      let pollution2Industry2Value = {}
      for (let i in newData) {
        let {pollution, industry} = newData[i]
        if (!pollution2Industry2Value[pollution])
          pollution2Industry2Value[pollution] = {}
        if (!pollution2Industry2Value[pollution][industry])
          pollution2Industry2Value[pollution][industry] = 0
        if (pollutions.indexOf(pollution) < 0)
          pollutions.push(pollution)
        if (industries.indexOf(industry) < 0)
          industries.push(industry)
        pollution2Industry2Value[pollution][industry]++
      }


      //按字母数字给pollutions数组排个序，避免出现series顺序在不同的数据下乱换的情况
      pollutions.sort()

      // 按行业所有pollution的总和值，给industry排序
      let industry2TotalValue = {}
      for (let pollution in pollution2Industry2Value) {
        for (let industry in pollution2Industry2Value[pollution]) {
          if (!industry2TotalValue[industry])
            industry2TotalValue[industry] = 0
          industry2TotalValue[industry] += pollution2Industry2Value[pollution][industry]
        }
      }
      industries.sort((industry1, industry2) => {
        return industry2TotalValue[industry1] - industry2TotalValue[industry2]
      })

      // 组织option
      this.options.yAxis.data = [...industries]
      this.options.color = (() => {
        let colors = []
        for (let i in pollutions)
          colors.push(pollutionType2Color[pollutions[i]])
        return colors

      })()
      for (let i in pollutions) {
        let pollution = pollutions[i]
        this.options.series.push({
          name: pollution,
          type: 'bar',
          stack: 'total',
          label: {
            show: true,
            formatter: (params) => {
              return params.data.value > 0 ? params.data.value : ''
            }
          },
          emphasis: {
            focus: 'series'
          },
          data: (() => {
            let retData = []
            for (let j in industries) {
              let industry = industries[j]
              if (pollution2Industry2Value && pollution2Industry2Value[pollution] && pollution2Industry2Value[pollution][industry])
                retData.push({value: pollution2Industry2Value[pollution][industry], pollution, industry})
              else
                retData.push({value: 0, pollution, industry})
            }
            return retData
          })()
        })
      }

      this.height = 20 * industries.length + 100
      this.$nextTick(() => {
        this.$refs.echart.chart.resize();

      })
    }
  }
}
</script>

<style lang="scss" scoped>
#pollutionContainer {
  $box-height: 100%;
  $box-width: 100%;
  padding: 5px;
  height: $box-height;
  width: $box-width;
  border-radius: 5px;
}
</style>
