<template>
  <div class="list-container" style="text-align: left;padding: 10px;height:100%;overflow-y: auto">
    <div>
      <strong>污染类型</strong>
      <CheckboxGroup v-model="pollutionValues" @on-change="dataChange($event,'pollutions')">
        <Checkbox v-for="item in pollutions" :label="item" :key="item">
          <div v-if="pollutionType2Color[item]" style="display: inline;border-radius: 3px;"
               :style="{background:pollutionType2Color[item]}">&nbsp;&nbsp;&nbsp;&nbsp;
          </div>
          <span>{{ item }}</span>
        </Checkbox>
      </CheckboxGroup>
      <divider size="small"></divider>
      <strong>行业类型</strong>
      <CheckboxGroup v-model="industryValues" @on-change="dataChange($event,'industries')">
        <Checkbox v-for="item in industries" :label="item" :key="item">
          <span>{{ item }}</span>
        </Checkbox>
      </CheckboxGroup>
    </div>
  </div>
</template>

<script>
export default {
  name: "eventFilter",
  data() {
    return {
      industries: [],
      pollutions: [],
      industryValues: [],
      pollutionValues: [],
      pollutionType2Color: {}
    }
  },
  methods: {
    dataChange() {
      this.$emit('dataChanged', {industryValues: this.industryValues, pollutionValues: this.pollutionValues})
    },
    setData(newData, keepFilterValues, pollutionType2Color) {
      let newIndustries = []
      let newPollutions = []
      for (let time in newData)
        for (let evnetId in newData[time]) {
          let event = newData[time][evnetId]
          if (newIndustries.indexOf(event.industry) < 0)
            newIndustries.push(event.industry)
          if (newPollutions.indexOf(event.pollution) < 0)
            newPollutions.push(event.pollution)
        }
      this.industries = [...newIndustries]
      this.pollutions = [...newPollutions]
      if (!keepFilterValues) {
        this.industryValues = [...newIndustries]
        this.pollutionValues = [...newPollutions]
      }
      this.pollutionType2Color = {...pollutionType2Color}
      this.dataChange()
    }
  }
}
</script>

